import React from 'react';
import PropTypes from 'prop-types'
import Product from './Product';
import './products.css';

class ProductList extends React.Component {

  static propTypes = {
    items: PropTypes.array
  }

  componentDidMount() {
    const {items, dataUrl} = this.props;

    if ((items && items.length > 0) || !dataUrl || dataUrl.length === 0) {
      return;
    }

    //fetch("https://next.json-generator.com/api/json/get/Ekhd3qgtH")
    fetch(dataUrl)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            items: result
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  }

  state = {
    items: []
  }

  render() {
    const body = this.getBody();

    return (<div>{body}</div>);
  }

  getBody = () => {
    const { isLoaded, error } = this.state;

    if (isLoaded) {

      if (error) {
        return <div className = "error">
                 <i className = "glyphicon glyphicon-warning-sign"></i>
                 { error.message }
               </div>;
      }

      const products = this.getProducts();

      return <ul>{ products }</ul>;
    }

    return <div id = "loader">
             <div className = "dot"></div>
             <div className = "lading"></div>
           </div>;
  }

  getProducts = () => {
    const stateItems = this.state.items;
    let productItems = this.props.items;

    if ( !productItems || productItems.length === 0 ) {

      if (!stateItems || !stateItems.length > 0) {
        return null;
      }

      productItems = stateItems;
    }

    return productItems.map((item) => 
              <li key = { item.id }>
                <Product item = { item }/>
              </li>
          );
  }

}

export default ProductList;